import React from "react";
import Button from "@ui/Button";
import Item from "@ui/item/Item";
import Tooltip from "@ui/Tooltip";
import Slider from "react-slick";
import PromotionCard from "@aem/marketing/marketing_common/card/PromotionCard";
import PropTypes from "prop-types";
import Link from "@ui/Link";
class CardFeature extends React.Component {
  constructor(props) {
    super(props);
    this.setting = {
      dots: false,
      infinite: false,
      slidesToShow: 3,
      draggable: true,
      responsive: [
        {
          breakpoint: 960,
          settings: {
            arrows: true,
            slidesToShow: 3,
            slidesToScroll: 3,
            infinite: true,
            dots: false,
          },
        },
        {
          breakpoint: 768,
          settings: {
            arrows: true,
            slidesToShow: 2,
            variableWidth: true,
          },
        },
        {
          breakpoint: 480,
          settings: {
            arrows: true,
            slidesToShow: 1,
            variableWidth: true,
          },
        },
      ],
    };
  }

  render() {
    return (
      <section
        className="plan-card"
        style={
          !!this.props.topBg
            ? {
                backgroundImage: `url(${this.props.topBg})`,
                backgroundSize: "cover",
                backgroundPosition: "bottom",
              }
            : null
        }
      >
        <div className="fui-container">
          {/* <h2 style={this.props.titleColor ? { color: this.props.titleColor } : null}>{this.props.title}</h2> */}
          <div
            className="full-width"
            dangerouslySetInnerHTML={{ __html: this.props.title }}
          ></div>
          <Slider
            {...this.setting}
            className={`horizontal-cards is-program is-rate-plan pb-md-9 mb-0 ${
              this.props.titleColor ? "is-white-arrow" : ""
            }`}
          >
            {this.props.cards.map((card, idx) => {
              switch (card.type) {
                case "promotion":
                  return <PromotionCard {...card} key={`plan-card-${idx}`} />;
                case "card01":
                  return (
                    <div
                      className="fui-card is-bg-white"
                      key={`plan-card-${idx}`}
                    >
                      <div className="align-left">
                        {card.image ? (
                          <div className="fui-card-image">
                            {card.imageLink ? (
                              <Link to={card.imageLink}>
                                <img src={card.image} alt={card.alt || "img"} />
                              </Link>
                            ) : (
                              <img src={card.image} alt={card.alt || "img"} />
                            )}
                          </div>
                        ) : null}
                        <div className="fui-card-caption pt-4">
                          <div className="fui-card-content">
                            {/* <h3 className="mb-3">{card.title}</h3> */}
                            <div
                              className={`${
                                card.description ? "mb-3" : "mb-8"
                              }`}
                              dangerouslySetInnerHTML={{ __html: card.title }}
                            ></div>
                            {card.description ? (
                              <div
                                className="fui-card-description mb-8"
                                dangerouslySetInnerHTML={{
                                  __html: card.description,
                                }}
                              ></div>
                            ) : null}
                            {card.list ? (
                              <div className="fui-list">
                                {card.list.map((item, i) => (
                                  <div
                                    className="item-with-tooltip"
                                    key={`plan-card-${idx}-item-${i}`}
                                  >
                                    <Item prefix="check">{item.text}</Item>
                                  </div>
                                ))}
                              </div>
                            ) : null}
                            <div
                              className="form-group m-0"
                              style={{
                                right: 0,
                                left: 0,
                                padding: "0 30px",
                                position: "absolute",
                              }}
                            >
                              {card.action ? (
                                <Button
                                  className="mt-4 mb-0 w-100"
                                  btnStyle={card.action.btnStyle}
                                  link={card.action.link}
                                  target={card.action.target}
                                  data-fet-key={card.fetKey}
                                >
                                  {card.action.text}
                                </Button>
                              ) : null}
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  );
                  break;
                default:
                  return (
                    <div className="fui-card" key={`plan-card-${idx}`}>
                      <div className="fui-card-action align-center">
                        <div className="fui-card-caption">
                          {card.image ? (
                            <img src={card.image} alt={card.alt || "img"} />
                          ) : null}
                          {card.ribbon ? (
                            <div className="fui-card-ribbon">{card.ribbon}</div>
                          ) : null}
                          <div className="fui-card-content">
                            <div
                              className="fui-card-title"
                              dangerouslySetInnerHTML={{ __html: card.title }}
                            ></div>
                            {card.description ? (
                              <div
                                className="fui-card-description"
                                dangerouslySetInnerHTML={{
                                  __html: card.description,
                                }}
                              ></div>
                            ) : null}
                            {card.list ? (
                              <div className="fui-list">
                                {card.list.map((item, i) => (
                                  <div
                                    className="item-with-tooltip"
                                    key={`plan-card-${idx}-item-${i}`}
                                  >
                                    <Item prefix="check">{item.text}</Item>
                                    {item.tooltip ? (
                                      <Tooltip
                                        content={
                                          <i className="icon-information"></i>
                                        }
                                        trigger="click"
                                        tooltip={item.tooltip}
                                      />
                                    ) : null}
                                  </div>
                                ))}
                              </div>
                            ) : null}
                          </div>
                          {card.action ? (
                            <div className="fui-card-extra">
                              <Button
                                btnStyle={card.action.btnStyle}
                                link={card.action.link}
                                target={card.action.target}
                              >
                                {card.action.text}
                              </Button>
                            </div>
                          ) : null}
                        </div>
                      </div>
                    </div>
                  );
              }
            })}
          </Slider>
        </div>
        <div
          className="bottom-bg"
          style={
            this.props.bottomBg
              ? { backgroundImage: `url(${this.props.bottomBg})` }
              : null
          }
        />
      </section>
    );
  }
}

CardFeature.propTypes = {
  title: PropTypes.string,
  titleColor: PropTypes.string,
  topBg: PropTypes.string,
  bottomBg: PropTypes.string,
  cards: PropTypes.arrayOf(
    PropTypes.shape({
      ribbon: PropTypes.string,
      image: PropTypes.string,
      title: PropTypes.string,
      list: PropTypes.arrayOf(
        PropTypes.shape({
          text: PropTypes.string,
          tooltip: PropTypes.string,
        })
      ),
      action: PropTypes.shape({
        text: PropTypes.string,
        link: PropTypes.string,
        target: PropTypes.string,
      }),
    })
  ),
};

export default CardFeature;
