import React from 'react';
import Button from '@ui/Button';
import PropTypes from 'prop-types';
class PromotionCard extends React.Component {
  render() {
    return (
      <div className={`fui-card is-guest is-promotion`}>
        <div
          className="fui-card-action"
          style={this.props.bg ? { backgroundImage: `url(${this.props.bg})` } : null}>
          <div className="fui-card-content">
            {/* <h4 className="">{this.props.title}</h4> */}
            <div dangerouslySetInnerHTML={{ __html: this.props.title }}></div>
            {
              this.props.action ? (
                <div className='fui-card-extra'>
                  <Button btnStyle='secondary' className="is-reverse is-text-accent" link={this.props.action.link} target={this.props.action.target} data-fet-key={this.props.fetKey}>
                    {this.props.action.text}
                  </Button>
                </div>
              ) : null
            }
          </div>
        </div>
      </div>
    )
  }
}
PromotionCard.propTypes = {
  bg: PropTypes.string,
  title: PropTypes.string.isRequired
}
export default PromotionCard;